#header {
  top: -120px;
  background-color: white;
  z-index: 100;
}

.header-section {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: .5rem 2rem;
  align-items: center;
}

#shmoo-npm {
  font-family: 'GubBlaBlo';
  font-size: 3rem;
  position: relative;
  top: 6px;
}

#shmoo-search {
  font-family: 'Fira Mono';
  color: rgba(0, 0, 0, 0.8);
  padding-left: 1.5rem;
  margin-left: 1rem;
  font-size: .875rem;
  font-weight: lighter;
  background-color: rgba(0, 0, 0, 0.05);
  width: 100%;
  height: 40px;
  line-height: 2.875;
}

#shmoo-button {
  font-family: 'Poppins';
  letter-spacing: 0.3px;
  font-size: .875rem;
  font-stretch: 100%;
  font-weight: lighter;
  font-weight: 400;
  line-height: 2.875;
  background: rgb(251, 62, 68);
  color: white;
  padding: 0 1rem;
  cursor: pointer;
  height: 40px;
}

#shmoo-img {
  height: 3rem;
}
