#random {
  border-width: 3px 0 0;
  border-style: solid;
  border-image: linear-gradient(139deg, #fb8817, #ff4b01, #c12127, #e02aff) 3;
  padding: 1rem 2.25rem;
  margin: 0 auto;
  max-width: 96rem;
  display: -webkit-flex;
  display: flex;
}

#heart {
  color: black;
  font-size: .875rem; /* don't forget to set rem! */
  margin-right: 1rem;
  padding-right: 1rem;
  line-height: 1.25;
  text-decoration: none;
}

#random-msg {
  font-family: 'Poppins', sans-serif;
  color: black;
  font-size: .875rem;
  text-align: left;
  margin-right: 2rem;
  text-decoration: none;
  font-weight: 400;
  letter-spacing: 0.015625em;
  line-height: 1.15;
  white-space: nowrap;
}
#header {
  top: -120px;
  background-color: white;
  z-index: 100;
}

.header-section {
  border: 1px solid rgba(0, 0, 0, 0.1);
  padding: .5rem 2rem;
  -webkit-align-items: center;
          align-items: center;
}

#shmoo-npm {
  font-family: 'GubBlaBlo';
  font-size: 3rem;
  position: relative;
  top: 6px;
}

#shmoo-search {
  font-family: 'Fira Mono';
  color: rgba(0, 0, 0, 0.8);
  padding-left: 1.5rem;
  margin-left: 1rem;
  font-size: .875rem;
  font-weight: lighter;
  background-color: rgba(0, 0, 0, 0.05);
  width: 100%;
  height: 40px;
  line-height: 2.875;
}

#shmoo-button {
  font-family: 'Poppins';
  letter-spacing: 0.3px;
  font-size: .875rem;
  font-stretch: 100%;
  font-weight: lighter;
  font-weight: 400;
  line-height: 2.875;
  background: rgb(251, 62, 68);
  color: white;
  padding: 0 1rem;
  cursor: pointer;
  height: 40px;
}

#shmoo-img {
  height: 3rem;
}

.project:hover {
  background: linear-gradient(rgb(255, 249, 229), rgb(250, 235, 235), rgb(250, 235, 249), rgb(237, 229, 255));
  border-radius: 5px;
}

.project, .experience {
  text-align: left;
  padding: 1rem;
}

.experience {
  padding-left: 0;
}

.project-title {
  margin-bottom: 0;
  margin-top: 0;
}

.project-subtitle {
  font-family: 'Fira Mono';
  font-size: .875rem;
  padding-bottom: .5rem;
}

.img-tech-box {
  -webkit-align-content: center;
          align-content: center;
}

.project-img {
  max-width: 90%;
}

.project-description {
  text-align: left;
}

.project-tech {
  font-family: 'Source Sans Pro';
  font-size: .875rem;
  color: white;
  margin: .8rem .2rem .5rem 0rem;
}

.tech {
  padding: 2px 5px;
  text-shadow: 0 1px rgba(0, 0, 0, 0.3);
}

.tech-key {
  background: #5B5B5B;
  border-radius: 3px 0 0 3px;
}

.tech-value-green {
  background: #04C504;
  border-radius: 0 3px 3px 0;
}

.tech-value-red {
  border-radius: 0 3px 3px 0;
  background: #E45637;
}

.description {
  margin: 0 1rem .5rem;
}

.description-key {
  font-weight: bold;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.description-value {
  margin: 0 1rem;
}

content {
  font-size: 18px;
  color: rgba(0, 0, 0, .85);
  width: 50vw;
  line-height: 1.65;
  letter-spacing: 0.1px;
}

.content-title {
  padding-bottom: 8px;
  border-bottom: 1px solid rgba(0, 0, 0, .1);
}
