.sidebar-subtitle {
  color: rgba(0, 0, 0, .5);
  margin: 1rem 0;
  -webkit-font-smoothing: auto;
  font-weight: bold;
}

.contact-bar {
  border: 1px #e8e8e8 solid;
  border-left: 35px #e1e1e1 solid;
  font-family: "Fira Mono";
  max-width: 95vw;
  line-height: 24px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: .875rem;
  margin-bottom: 1rem;
  color: rgba(0, 0, 0, .8);
  letter-spacing: 0px;
  padding: .35rem 1rem .2rem;
  border-radius: .25rem;
}

.contact-icon {
  height: 1.5rem;
  left: .35rem;
}

.current-obsession {
  padding: 0 .5rem .25rem 0;
  border-bottom: 2px solid #EAD4FF;
  /* darker purple: #9826FF or maybe #8956FF */
  /* more specifically: rgb(137, 86, 255) and rgba(137, 86, 255, .2) */
}

.obsession-text {
  text-align: left;
  font-size: 1.25rem;
  color: rgba(0, 0, 0, .8);
  font-family: 'Source Sans Pro';
  font-weight: 600;
}

#obsession-svg {
  position: relative;
  top: 5px;
  right: -8px;
  width: 170px;
  height: 40px;
}

.all-skills {
  height: 12rem;
  overflow: scroll;
}

.skill-subtitle {
  padding-bottom: .5rem;
  margin: .5rem 0;
}

.skills-list {
  padding-bottom: .5rem;
  color: red;
}

.skill-items {
  margin-right: .875rem;
}
ul {
  margin: 0;
  padding: 0;
  width: 100%;
  list-style-type: none;
}

li.tab {
  border-bottom: 2px solid black;
  width: 100%;
  font-weight: 600;
  -webkit-font-smoothing: auto;
}

li.tab1 {
  color: rgb(158, 119, 0);
  border-color: #FFCD3A;
  background-color: rgba(255, 205, 58, 0.2);
}
li.tab1.not-selected:hover {
  background-color: rgb(255, 249, 229);
}

li.tab2 {
  color: #cb3837;
  border-color: #cb3837;
  background-color: rgba(203, 56, 55, 0.2);
}
li.tab2.not-selected:hover {
  background: rgb(250, 235, 235);
}

li.tab3 {
  color: rgb(120, 33, 117);
  border-color: #C836C3;
  background-color: rgba(200, 54, 195, 0.2);
}
li.tab3.not-selected:hover {
  background: rgb(250, 235, 249);
}

li.tab4 {
  color: rgb(41, 0, 138);
  border-color: #8956FF;
  background-color: rgba(137, 86, 255, 0.2);
}
li.tab4.not-selected:hover {
  background: rgb(237, 229, 255);
}

li.not-selected {
  background-color: white;
}

.ntt {
  background: white;
  z-index: 10;
}

.name-title {
  top: 0;
  background: white;
  padding: 1.5rem 0;
}

#name {
  letter-spacing: -0.8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 2rem;
  font-weight: 1000;
  line-height: 1.15;
}

#title {
  font-family: 'Fira Mono';
  margin-top: .5rem;
  white-space: nowrap;
  font-size: .875rem;
  color: rgba(0, 0, 0, 0.8);
}

#tabs {
  position: -webkit-sticky;
  position: sticky;
  margin-bottom: 2rem;
}
#main {
  width: 81%;
  margin: 2rem auto 4rem;
}

#footer-container {
  border-top: solid 1px rgba(0, 0, 0, .1);
}

#footer {
  height: 80px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

#footer-banner {
  height: 10px;
  width: 100%;
  background-image: linear-gradient(139deg, #fb8817, #ff4b01, #c12127, #e02aff);
}

body {
  margin: 0;
  font-family: 'Source Sans Pro', sans-serif;
  color: rgba(0, 0, 0, .85);
  -webkit-font-smoothing: antialiased;
}

a {
  text-decoration: none;
  color: inherit;
  display: block;
}

li {
  padding: .7rem 2rem;
  border-radius: 5px 5px 0 0;
  cursor: pointer;
  display: block;
  text-align: center;
}

.flex {
  display: -webkit-flex;
  display: flex;
}

.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}

.flex-wrap {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.space-between {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.sticky {
  position: -webkit-sticky;
  position: sticky;
}

.relative {
  position: relative;
}

.absolute {
  position: absolute;
}

.green {
  color: rgb(25, 169, 116);
  letter-spacing: 0;
}

.highlight {
  font-family: 'Fira Mono';
  font-size: .875rem;
  line-height: 1.15;
  padding: .125rem .25rem;
  border-radius: 3px;
  background: rgba(0, 0, 0, .1);
}

